/* global document */
import 'typeface-roboto';
import React, { lazy, PureComponent, Suspense } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import { MuiThemeProvider } from '@material-ui/core/styles';
import * as serviceWorker from './serviceWorker';
import Loading from './components/Loading';
import NewsletterService from './services/newsletter';
import Storage from './utils/storage';
import theme from './theme';

const Home = lazy(() => import('./pages/Home'));
const Settings = lazy(() => import('./pages/Settings'));
const Newsletters = lazy(() => import('./pages/Newsletters'));
const Issues = lazy(() => import('./pages/Issues'));
const load = (Page, onChange) => p => <Page {...p} onChange={onChange} />;

class App extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isDarkMode: false
    };
  }

  async componentDidMount() {
    await this.loadNewsletters();
    await this.setDarkModeState();
  }

  onChangeDarkMode = () => {
    this.setDarkModeState();
  }

  loadNewsletters = async () => {
    try {
      const newsletters = await NewsletterService.list();
      await Storage.set('newsletters', newsletters);
    } catch (err) {
      console.log(err);
    }
  }

  setDarkModeState = async () => {
    try {
      const isDarkMode = await Storage.get('isDarkMode');
      this.setState({ isDarkMode: !!isDarkMode });
    } catch (err) {
      this.setState({ isDarkMode: false });
    }
  }

  render() {
    const { isDarkMode } = this.state;
    return (
      <MuiThemeProvider theme={theme(isDarkMode)}>
        <CssBaseline />
        <BrowserRouter>
          <Suspense fallback={<Loading />}>
            <Switch>
              <Route exact path="/" component={load(Home)} />
              <Route path="/newsletters" component={load(Newsletters)} />
              <Route path="/issues/:id" component={load(Issues)} />
              <Route path="/settings" component={load(Settings, this.onChangeDarkMode)} />
            </Switch>
          </Suspense>
        </BrowserRouter>
      </MuiThemeProvider>
    );
  }
}

ReactDOM.render(<App />, document.getElementById('root'));

serviceWorker.unregister();
