import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import HomeIcon from '@material-ui/icons/Home';
import SettingsIcon from '@material-ui/icons/Settings';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import styles from './styles';

const Header = memo((props) => {
  const { title, classes, backButton } = props;
  return (
    <AppBar position="fixed" color="primary">
      <Toolbar>
        {
          backButton ?
            <IconButton className={classes.menuLeft} color="inherit" onClick={backButton}>
              <ArrowBackIcon />
            </IconButton>
            :
            <IconButton className={classes.menuLeft} color="inherit" component={Link} to="/">
              <HomeIcon />
            </IconButton>
        }
        <Typography variant="h6" color="inherit" className={classes.main}>
          {title || 'Home'}
        </Typography>
        <IconButton color="inherit" component={Link} to="/settings">
          <SettingsIcon />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
});

Header.propTypes = {
  title: PropTypes.string,
  classes: PropTypes.object,
  backButton: PropTypes.func
};

export default withStyles(styles)(Header);
