import Api from '../utils/api';

const Newsletter = {
  list() {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await Api.get('/newsletters');
        const newsletters = res.data || [];
        return resolve(newsletters);
      } catch (err) {
        return reject(err);
      }
    });
  },
  listIssues(newsletterId = '') {
    return new Promise(async (resolve, reject) => {
      try {
        const options = newsletterId ? { params: { newsletterId: newsletterId.join(',') } } : {};
        const res = await Api.get('/newsletters/issues', options);
        const issues = res.data || [];
        return resolve(issues);
      } catch (err) {
        return reject(err);
      }
    });
  },
  getIssueContentById(id) {
    return new Promise(async (resolve) => {
      try {
        if (id) {
          const res = await Api.get(`/newsletters/issues/${id}`);
          return resolve(res.data);
        }
        return resolve();
      } catch (err) {
        return resolve();
      }
    });
  }
};

export default Newsletter;
