import { Plugins } from '@capacitor/core';

const Storage = {
  get(key) {
    return new Promise(async (resolve, reject) => {
      try {
        const { value } = await Plugins.Storage.get({ key });
        return resolve(JSON.parse(value));
      } catch (err) {
        return reject(err);
      }
    });
  },
  async set(key, value) {
    return new Promise(async (resolve, reject) => {
      try {
        await Plugins.Storage.set({ key, value: JSON.stringify(value) });
        return resolve(true);
      } catch (err) {
        return reject(err);
      }
    });
  },
  remove(key) {
    return new Promise(async (resolve, reject) => {
      try {
        await Plugins.Storage.remove({ key });
        return resolve(true);
      } catch (err) {
        return reject(err);
      }
    });
  },
  clear() {
    return Plugins.Storage.clear();
  }
};

export default Storage;
